@import '../../node_modules/bootstrap/dist/css/bootstrap.min.css';


.root {
    background: white;
    padding: 20px;
    box-shadow: 0 0 12px #888888;
}

@media (min-width: 480px) {
  .root {
      margin-top: 5%;
  }
}

@media (max-width: 480px) {
  .root {
      font-size: 0.8em;
  }
}

body {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #f6f5f7;
    font-size: 16px;
}

input:focus,
input:active,
button:active,
button:focus {
  outline: none;
}

:focus {
  outline: none;
}

a, a:hover {
  color: inherit;
}

.dark-theme {
  color: white;
  background-color: grey;
}

.completed {
text-decoration: line-through;
}